import { Button } from '@chakra-ui/button'
import { useColorMode } from '@chakra-ui/color-mode'
import { Image } from '@chakra-ui/image'
import { Box, Flex, Heading, Stack, Text } from '@chakra-ui/layout'
import { Collapse } from '@chakra-ui/transition'
import { useCycle } from 'framer-motion'
import React, { useState } from 'react'

export default function Project(props) {
    const [open, toggleOpen] = useCycle(false, true)
    let {colorMode, toggleColorMode} = useColorMode();

    return (
        <Box>
            <Stack
            borderWidth={1}
            borderColor={colorMode === 'light' ? 'gray.100' : 'gray.600'}
            p={4}
            direction='column'
            align='center'
            borderRadius={16}
            shadow="base"
            spacing={12}
            >
                <Heading textAlign='center'>{props.title}</Heading>
                <Flex h={96} w='100%' justify='center' align='center'>
                    {props.src ? <Image src={process.env.PUBLIC_URL + 'project_photos/' + props.src} maxH={96} borderRadius={16}/> : ''}
                </Flex>
                <Box w='100%'>
                    <Collapse in={open} startingHeight={108}>{props.children}</Collapse>
                </Box>
                <Button onClick={toggleOpen}>{open ? "Show Less" : "Show More"}</Button>
            </Stack>
        </Box>
    )
}
