import React, { useState } from 'react'
import { Box, Button, Center, Flex, Heading, Icon, PopoverTrigger, Spacer, Stack, Text, Textarea } from '@chakra-ui/react'
import { QuestionOutlineIcon } from '@chakra-ui/icons'
import ExplainButton from './ExplainButton'
import * as tf from '@tensorflow/tfjs'
import { loadLayersModel } from '@tensorflow/tfjs'
import axios from 'axios'

export default function OnionConnoisseur() {
    const [enteredArticle, setEnteredArticle] = useState('');
    const [loading, setLoading] = useState(false);
    const [result, setResult] = useState(false);
        
    const onTextFieldChange = (e) => {
        setEnteredArticle(e.target.value);
    }

    const predict = () => {
        setLoading(true);
        setResult(false);
        let formData = new FormData();
        formData.append('article-body', enteredArticle);
        axios.post('https://onion-connisseur.herokuapp.com/', formData).then(res => {
            console.log(res.data)
            setLoading(false)
            setResult(res.data);
        });

    }

    return (
        <Center>
            <Stack spacing={8} align='center'>
                <Flex align='center'>
                    <Heading>The Onion Connoisseur</Heading>
                    <Box ml={2}>
                        <ExplainButton />
                    </Box>
                    </Flex>
                <Textarea placeholder="Enter your article here" w='100%' onChange={onTextFieldChange} minHeight={64}></Textarea>
                {
                    (result) ? 
                    <Flex justify='center'><Text>Your article has been determined as {result < 0.5 ? <Text as='span' color='green'>real</Text> : <Text as='span' color='red'>fake</Text>} with a score of {result}.</Text></Flex>
                    : ''
                }
                <Button w={'60%'} onClick={predict} isLoading={loading} loadingText="This will take a second!">Is it satire?</Button>
            </Stack>
        </Center>
    )
}
