import { Button } from '@chakra-ui/button'
import { Stack, Text, Heading, Box, Flex } from '@chakra-ui/layout'
import { Tooltip } from '@chakra-ui/tooltip'
import React from 'react'
import TTWord from './TTWord'

export default function IntroStack(props) {
    return (
        <Flex h='95vh' pt='10vh'>
            <Stack w='100%' align='center' spacing={6}>
                <Heading textAlign='center'>Hello!</Heading>
                <Heading textAlign='center'>I love learning about computer science and <TTWord label="math">but the words "open set" really, really scare me.</TTWord>!</Heading>
                <Button textAlign='center' onClick={props.scrollTo}>Learn a bit about me!</Button>
            </Stack>
        </Flex>
    )
}
