import logo from './logo.svg';
import './App.css';
import { HashRouter, Link, Route, Switch } from 'react-router-dom';
import OnionConnoisseur from './components/onion-connisseur/OnionConnoisseur';

import React, { useEffect } from 'react';
import { Box, Stack, Text } from '@chakra-ui/layout';
import Home from './components/home/Home';

import { useMediaQuery } from 'react-responsive'
import MobileNavbar from './components/navbar/MobileNavbar';
import LargerNavbar from './components/navbar/LargerNavbar';
import Projects from './components/projects/Projects';
import Footer from './components/footer/Footer';



function App() {
  const mobileNav = useMediaQuery({query: '(min-device-width: 48em)'})
  return (
    <HashRouter>
      <Stack direction='column' spacing={'5vh'}>
      {
      mobileNav ? 
      <LargerNavbar w='75%' />
      :
      <MobileNavbar/>
      }
      <Box minHeight='100vh'>
        <Switch>
          <Route exact path='/' component={Home} />
          <Route exact path='/projects' component={Projects} />
          <Route exact path='/onion_connoisseur' component={OnionConnoisseur} />
        </Switch>
      </Box>
      <Footer />
      </Stack>
      </HashRouter>
  )
}

export default App;
