import React from 'react'
import {
    Drawer,
    DrawerBody,
    DrawerFooter,
    DrawerHeader,
    DrawerOverlay,
    DrawerContent,
    DrawerCloseButton,
    Button,
    Text,
    IconButton,
    useColorMode,
    Stack,
    FormControl,
    FormLabel,
    Switch,
    Box,
  } from "@chakra-ui/react"
import { Link } from 'react-router-dom';
import { MoonIcon } from '@chakra-ui/icons';

function NavbarLink(props) {
    return (
        <Box onClick={props.onClick} pb={2} borderBottomWidth={1} borderBlockEndColor='gray.100'><Link to={props.to}><Text as='b' fontSize='md' display='block'>{props.children}</Text></Link></Box>
    )
}

export default function MobileDrawer(props) {
    let { isOpen, onClose } = props;
    let { colorMode, toggleColorMode } = useColorMode();
    return (
        <Drawer isOpen={isOpen} onClose={onClose} placement="right">
            <DrawerOverlay>
                <DrawerContent>
                    <DrawerCloseButton onClick={onClose} />
                    <DrawerHeader>Navigation Menu</DrawerHeader>

                    <DrawerBody>
                        <Stack direction='column' spacing={4}>
                            <NavbarLink onClick={onClose} to='/'>Home</NavbarLink>
                            <NavbarLink onClick={onClose} to='/projects'>Projects</NavbarLink>
                            <NavbarLink onClick={onClose} to='/onion_connoisseur'>Featured App</NavbarLink>
                        </Stack>
                    </DrawerBody>

                    <DrawerFooter>
                    <FormControl display="flex" alignItems="center">
                            <FormLabel htmlFor="dark-mode" mb="0">
                                Enable Dark Mode 
                            </FormLabel>
                        <Switch id="dark-mode" isChecked={colorMode === 'dark'} onChange={toggleColorMode}/>
                    </FormControl>

                    </DrawerFooter>
                </DrawerContent>
                </DrawerOverlay>
        </Drawer>
    )
}
