import { MoonIcon } from '@chakra-ui/icons'
import { Text } from '@chakra-ui/layout'
import React from 'react'
import { Link } from 'react-router-dom'
import NavbarContainer from './NavbarContainer'
import { useColorMode } from '@chakra-ui/color-mode';
import { IconButton } from '@chakra-ui/button'


export default function LargerNavbar() {
    const { colorMode, toggleColorMode } = useColorMode()

    return (
        <NavbarContainer justify='space-evenly' w='60%'>
            <Link to='/'><Text as='b' fontSize='md' display='block'>Home</Text></Link>
            <Link to='/projects'><Text as='b' fontSize='md' display='block'>Projects</Text></Link>
            <Link to='/onion_connoisseur'><Text as='b' fontSize='md' display='block'>Featured App</Text></Link>
            <IconButton onClick={toggleColorMode} bg='transparent'><MoonIcon /></IconButton>
      </NavbarContainer>
    )
}
