import { Stack, Text, Heading, Box, Flex } from '@chakra-ui/layout'
import { Tooltip } from '@chakra-ui/tooltip'
import React, { useRef } from 'react'
import AboutStack from './AboutStack'
import HomeDivider from './HomeDivider'
import IntroStack from './IntroStack'
import ProjectsStack from './ProjectsStack'
import TTWord from './TTWord'

export default function Home() {
    let ref = useRef(null)
    let scrollToElement = () => {
        if (ref && ref.current) {
            ref.current.scrollIntoView()    
        }
    }
    return (
        <Stack w='100%' align='center' direction='column' spacing={16}>
            <Stack direction='column' w={['90%']} align='center'>
                <IntroStack scrollTo={scrollToElement} />
                <HomeDivider/>
                <AboutStack ref={ref}/>
                <ProjectsStack />
            </Stack>
        </Stack>
    )
}
