import { IconButton } from '@chakra-ui/button'
import { QuestionOutlineIcon } from '@chakra-ui/icons'
import { Text } from '@chakra-ui/layout'
import { Popover, PopoverArrow, PopoverBody, PopoverCloseButton, PopoverContent, PopoverHeader, PopoverTrigger } from '@chakra-ui/popover'
import React from 'react'
import TTWord from '../home/TTWord'

export default function ExplainButton() {
    return (
        <Popover>
            <PopoverTrigger>
                <IconButton size='small'><QuestionOutlineIcon /></IconButton>
            </PopoverTrigger>
            <PopoverContent>
                <PopoverArrow />
                <PopoverCloseButton />
                <PopoverHeader as='b'>
                    What is this?
                </PopoverHeader>
                <PopoverBody>
                    <Text>This is a fun little program I created with my friend for our final project in SI 206 at the University of Michigan.</Text>
                    <Text>We use a machine learning algorithm to determine if a provided article is likely to be satire. Try it below by pasting an <TTWord label='article'>entire</TTWord> below!</Text>
                </PopoverBody>
            </PopoverContent>
        </Popover>
    )
}
