import { Heading, Link as OutLink, SimpleGrid, Stack, Text } from '@chakra-ui/layout'
import React from 'react'
import Project from '../projects/Project'
import { Link } from 'react-router-dom'
import { useColorMode } from '@chakra-ui/color-mode'
import { Button } from '@chakra-ui/button'

export default function ProjectsStack() {

    let { colorMode, toggleColorMode } = useColorMode();

    return (
        <Stack
        direction='column'
        align='center'
        w='100%'
        pt={24}
        spacing={12}
        >
            <Heading>Projects</Heading>
            <SimpleGrid columns={[1, 1, 3, 3]} gap={12}>
                <Project title="Smart Mirror" src='Mirror.png'>
                    <Text>This is a smart mirror I made. I got inspiration from a YouTube video, but the parts in the video proved to be too expensive, and I did not have a 3D printer at the time to print the parts. So, I decided to see if I could build the mirror for less money and with fewer tools. The answer was yes! The mirror works by using a device that outputs to a recycled monitor behind the glass. The glass has a one-way mirror film on it to give the mirror effect. I manually cut all of the wood because I didn’t have electric tools, so some of the structure came out a bit uneven. This was my first real introduction to woodworking, and I think it came out good despite that. The software is a web page served by an Angular server and a Node.js backend. The backend also has Spotify integration. A Raspberry Pi works well as the video input, but anything that can display a webpage also works. In the end, the project helped me develop my woodworking and IOT skills, and I ended up with a pretty cool mirror.</Text>
                </Project>
                <Project title="COVID-19 Check In App" src="FDS_DESIGNER_CROP.png">
                    <Text>This is a cross-platform app made in Flutter that allows for the delivery of customized forms in health settings. The project started when I was working with a medical research team at the University of Michigan. Due to the current pandemic, the team I worked with asked me to build a COVID-19 focused application to manage appointments. I used Flutter because it provided ease of development for cross-platform applications as well as fast rendering. I first started with a quick proof-of-concept app that I then built into an app with 3 main sections — login, appointments, and check-ins. During check-ins, each answer can progress to the next question, ask another question, or switch to a different question set depending on the data from the backend. Our team has since migrated to using the Responsiblue Health Check-In System.</Text>
                </Project>
                <Project title="Onion Connoisseur" src='onion_connoisseur.png'>
                    I created this project with my friend as our final project for SI 206 (Data-Oriented Programming) at the University of Michgian. The algorithm uses a neural network to determine wheter an article is satire or not. You can try this application out for yourself <Link to='/onion_connoisseur'><Text as='span' color= {colorMode === 'light' ? 'blue' : 'blue.200'}>here</Text></Link>. Additionally, you can find source code on our <OutLink color= {colorMode === 'light' ? 'blue' : 'blue.200'} href='https://github.com/efea-umich/SI-206-Final'>GitHub repository</OutLink> and more information on the project <OutLink color= {colorMode === 'light' ? 'blue' : 'blue.200'} href='https://github.com/efea-umich/SI-206-Final/raw/master/Final_Project_Report.pdf'> on our project report</OutLink>.
                </Project>
            </SimpleGrid>
            <Link to='/projects'><Button>View more projects here!</Button></Link>
        </Stack>
    )
}
